<template>
  <!-- 数据汇总页面 -->
  <a-layout class="container">
    <a-row style="margin: 10px;" justify="center">
      <a-col class="gutter-row" :span="6">
        <span> 主集合名 </span>
        <a-select
          v-model:value="mainSchema"
          :options="mainSchemas"
          style="width: 200px"
        ></a-select>
      </a-col>
      <a-col class="gutter-row" :span="6">
        <span> 主集合属性 </span>
        <!--
          :max-tag-count="3" 设置最多显示 3 个选项
        -->
        <a-select
          v-model:value="mainField"
          :max-tag-count="3"
          :options="mainFields"
          mode="multiple"
          style="width: 200px"
        >
        </a-select>
      </a-col>
      <a-col class="gutter-row" :span="6">
        <span> 补充集合名 </span>
        <a-select
          v-model:value="subSchema"
          :options="subSchemas"
          style="width: 200px"
        ></a-select>
      </a-col>
      <a-col class="gutter-row" :span="6">
        <span> 补充集合属性 </span>
        <!--
          :max-tag-count="3" 设置最多显示 3 个选项
        -->
        <a-select
          v-model:value="subField"
          :max-tag-count="3"
          :options="subFields"
          mode="multiple"
          style="width: 200px"
        ></a-select>
      </a-col>
    </a-row>
    <div style="margin: 6px auto;">
      <!-- 清空键 -->
      <a-button
        shape="round" 
        style="width: 100px; margin: 6px;"
        @click="allClear"
      >
        <template #icon>
          <clear-outlined />
        </template>
        清空
      </a-button>
      <!-- 搜索键 -->
      <a-button 
        type="primary" 
        shape="round" 
        :loading="loading"
        style="width: 200px; margin: 6px;"
        @click="search(1, pageSize)"
      >
        <template #icon>
          <search-outlined />
        </template>
        搜索选定内容
      </a-button>
    </div>
    <!-- 分页器 -->
    <a-pagination 
      v-model:current="current" 
      v-model:pageSize="pageSize"
      :total="infoTotal"
      :showSizeChanger="false"
      :show-total="total => `总共有 ${total} 条记录`"
      show-less-items
      show-quick-jumper
      class="pagination"
      @change="(page, pageSize) => pageChange(page, pageSize)"
    />

    <!-- 分割线 -->
    <a-divider class="divider"></a-divider>

    <!-- 数据集合 -->
    <a-table
      :dataSource="infoGroup" 
      :columns="columns"
      :pagination="false"
      :loading="loading"  
      size="small"
      style="margin-left: 10px; margin-right: 10px;"
      bordered
    >
      <template #bodyCell="{ column, text, record }">
        <template v-if="column.dataIndex === 'mainSchema'">
          <a-tag @click="() => selectRecord(text)"> 主集合记录 1 </a-tag>
        </template>
        <template v-else-if="column.dataIndex === 'subSchema'">
          <template v-for="(item, index) in text" :key="index">
            <a-tag @click="() => selectRecord(item)"> 补充集合记录 {{ index + 1 }} </a-tag>
          </template>
        </template>
      </template>
    </a-table>
    
    <!-- 提示等 -->
    <!-- 记录具体内容 -->
    <a-modal
      v-model:visible="recordVisible" 
      title="记录详情"
      okText="确定"
      cancelText=" "
      :destroyOnClose=true
      @ok="() => recordVisible = false"
    >
      <a-list bordered :data-source="selectedRecord">
        <template #renderItem="{ item }">
          <a-list-item>
            <div>{{ item.schema }}-{{ item.field }}</div>  
            <div>
              {{ item.value }} 
              <copy-two-tone @click="() => copyText(item.value)"/>
            </div>
          </a-list-item>
        </template>
      </a-list>
    </a-modal>
  </a-layout>
</template>

<script setup>
import { computed, onMounted, ref } from 'vue';
import axios from 'axios';
import { ClearOutlined, SearchOutlined, CopyTwoTone } from '@ant-design/icons-vue';
import { message } from 'ant-design-vue';
import { copyText } from '@/utils/copyText';

// 记录总条数
const infoTotal = ref(0);
// 当前页
const current = ref(1);
// 每页条数
const pageSize = ref(10);

// 信息集合
const infoGroup = ref([]);

// 类型数据
const typeDatas = ref([]);

// 选中主集合名
const mainSchema = ref("");
// 选中主集合属性
const mainField = ref([]);
// 选中补充集合名
const subSchema = ref("");
// 选中补充集合属性
const subField = ref([]);

// 当前选中的记录
const selectedRecord = ref([]);

// 记录显示情况
const recordVisible = ref(false);

// 搜索执行情况
const loading = ref(false);

// 主集合名
const mainSchemas = ref([]);
// 主集合属性
const mainFields = computed(() => {
  return typeDatas.value.find(val => val.schemaName == mainSchema.value)?.fields.map(val => {
    return {value: val.fieldName};
  });
});
// 补充集合名
const subSchemas = ref([]);
// 补充集合属性
const subFields = computed(() => {
  return typeDatas.value.find(val => val.schemaName == subSchema.value)?.fields.map(val => {
    return {value: val.fieldName};
  });
});

// 表格列的配置描述
const columns = [
  {
    title: "序号",
    dataIndex: "key",
    key: "key",
  },
  {
    title: "主集合",
    dataIndex: "mainSchema",
    key: "mainSchema",
  },
  {
    title: "补充集合",
    dataIndex: "subSchema",
    key: "subSchema",
  }
]

// 创建信息集合
function createInfoGroup(data) {
  infoGroup.value = data.map((info, index) => {
    const mainSchemaInfo = [];
    const subSchemaInfo = [];
    for(let val of info) {
      if(!Array.isArray(val)) mainSchemaInfo.push(val);
      else subSchemaInfo.push(val);
    }
    return {
      key: (current.value - 1) * pageSize.value + index + 1,
      mainSchema: mainSchemaInfo,
      subSchema: subSchemaInfo
    }
  });
}

// 清空搜索条件
function allClear() {
  mainSchema.value = "";
  mainField.value = [];
  subSchema.value = "";
  subField.value = [];
}

// 搜索
function search(page, pageSize) {
  // 后端接入后使用
  loading.value = true;
  /* 
    请求中含有数组对象
    使用 post 请求发送
    get 请求有点麻烦 
  */
  axios.post("dataShow.do", {
    page: page,
    pageSize: pageSize,
    value: [
      {schema: mainSchema.value, fields: [...mainField.value]},
      {schema: subSchema.value, fields: [...subField.value]}
    ]
  }).then((result) => {
    infoTotal.value = result.data.infoTotal;
    createInfoGroup(result.data.res);
    loading.value = false;
  }).catch((error) => {
    message.error("搜索失败");
    console.log(error);
    loading.value = false;
  })
}

// 页数改变 
function pageChange(page, pageSize) {
  search(page, pageSize);
}

// 选中记录
function selectRecord(record) {
  selectedRecord.value = record;
  recordVisible.value = true;
}

// 在组件挂载完成后执行
onMounted(() => {
  axios.get('getType.do')
    .then((result) => {
      typeDatas.value = result.data.map(val => {
        val.fields = val.fields.map(field => ({
          "fieldName": field
        }));
        return {
          "schemaName": val.schemaName,
          "fields": val.fields
        };
      });
      mainSchemas.value = typeDatas.value.map(val => {
        return {value: val.schemaName};
      });
      subSchemas.value = typeDatas.value.map(val => {
        return {value: val.schemaName};
      });
    })
    .catch((error) => {
      console.log(error);
    })
});
</script>

<style scoped>
.container {
  height: 100%;
}

.pagination {
  margin: 10px auto;
}

.divider {
  margin-top: 0;
  /* border-color: #1890ff; */
}

@media (max-height: 480px) {
  /* .container {
    display: none;
  } */
}
</style>